
import $ from 'jquery'
// import ScrollReveal from 'scrollreveal'

import Highway from '@dogstudio/highway';
import Fade from './transition';






$( document ).ready(function() {

    // $('body').html('Hello World, if you see this message Parcel Bundler and Github Hosting are working nicely!');

    
    // ScrollReveal().reveal('.textBlock', {delay: 500})
    
    const H = new Highway.Core( {
        transitions: {
            default: Fade
        }
    })
    
    $('.moebelSubpage').on('click', function() {
        $('.subMenu').toggleClass('collapse');
    })




      // yellow animation

  var window_height = $(window).height();
  var document_height = $(".generalContainer").height();
  var to_be_scrolled = 0;
  var scrolled = 0;
  var percentage = 0;
  var start_after = 0;
  var scroll_container = $("#detail_container");
  var scroll_progress_el2 = $("#control_title2");
  var clrperc = 0;

  //   on scroll
  $(document).scroll(function () {
      fade_bkg();
    
  });

  // do animation
  function fade_bkg() {
      console.log("Lklkl")

    to_be_scrolled = document_height - window_height;
    scrolled = $(document).scrollTop() + 39;

    start_after = to_be_scrolled * 0.2;
    if (scrolled > start_after) {
      percentage = ((scrolled - start_after) * 100) / (to_be_scrolled - start_after);
      percentage = percentage * 0.02;

      clrperc = Math.round(Math.pow(10, percentage));
      clrperc = clrperc / 100;

      $("html, .generalContainer").css("background-color", "rgba(255, 244, 231," + clrperc + ")");
      $(".headerOuterWrapper").css("background-color", "rgba(255, 234, 208," + clrperc + ")");
    }

    return scrolled;
  }






});

